import React from 'react'

import { Box, Heading, Text , SimpleGrid,GridItem, Image,HStack, Badge, Link} from '@chakra-ui/react'

import style from './Project.module.css'

import { ExternalLinkIcon } from '@chakra-ui/icons'

const Projects = () => {
  return (
    <Box border={'2px solid red'}  id="projects" >

            <Box mt = '110px' border={'2px solid green'}>

            <Heading color = 'rgb(0, 255, 162);' fontSize={35}> <Text as = 'span' color = 'white'>My</Text> Projects</Heading>

            <SimpleGrid mt = '20px' columns={1}>

                  <GridItem className="project-card" bg = 'blue.700' border = '2px solid red' px = '10px' py = '25px'>




                      {/* visible on tablet,mobile screen description box */}


                      <Box flexDirection = 'column' border = '2px solid green' display = {{base : 'flex','1349px' : 'none'}}>

                                  <HStack justify = 'center'>

                                    <Heading className="project-title" fontSize={'23px'}>Pharmony </Heading>

                                    <Badge  fontSize={10} colorScheme={'green'}>clone : 1mg</Badge>

                                  </HStack>

                                  <Box m = 'auto' display = 'flex' flexDirection = 'column' w = '80%' bg = 'blue.800' borderRadius={'10px'} mt = '20px' p = '2'>

                                        <Text color = 'aqua' align = 'left'>Description : </Text>

                                        <Text className ="project-description" align = 'left'>It is an Indian healthcare and wellness platform that offers a range of online healthcare services to consumers.</Text>

                                        <Text mt = '5px' color = 'aqua' align = 'left'>Tech Stack : </Text>

                                        <HStack mt = '10px' className="project-tech-stack" mt = '5px'>

                                                <Image w = '20px' src = 'HTML.png'></Image>

                                                <Image w = '25px' src = 'CSS.png'></Image>

                                                <Image borderRadius={'50%'} w = '25px' src = 'Chakra ui.png'></Image>

                                                <Image borderRadius={'50%'} w = '25px' src = 'React.png'></Image>

                                                <Image w = '25px' src = 'react-router.png'></Image>

                                        </HStack>

                                        <Text mt = '10px' color = 'aqua' align = 'left'>Deployed Link : </Text>



                                        <HStack mt = '10px'>

                                            <Link isExternal href = '#'><Image _hover = {{cursor : 'pointer'}} border = '2px solid' borderRadius={'10px'} bg = 'white' w = '50px' src = 'Netlify logo.png'></Image></Link>

                                            <ExternalLinkIcon zIndex = '2' />

                                            <Link isExternal href = '#'><Image borderRadius = '40px' bg = 'white' _hover = {{cursor : 'pointer'}} borderRadius={'10px'} w = '30px' src = 'github.png'></Image></Link>

                                            <ExternalLinkIcon zIndex = '2' />


                                          </HStack>




                                  </Box>


                              </Box>




                        <HStack flexDirection = {{base : 'column','848px' : 'row'}} border = '2px solid' justify = {{base : 'center','1349px' : ''}}  position={'relative'}>

                                  <Image  zIndex={'2'} w = {{'base' : '95%','848px' : '42.4%'}} src = 'Laptop2.png'></Image>

                                  <Image  id = {style['Project-scr']} top = {{'base' : '5.6%','848px' : '22%' ,'1349px' :'16.4%'}} left = {{base : '13%','496px' : '14%','848px' : '23.2%','1011px' : '23.5%','1349px' :'5.1%'}} w = {{base : '70%','848px' : '30.9%'}} position={'absolute'} src = 'Pharmony+.png'></Image>

                                <HStack display = {{base : 'none','1349px' : 'flex'}} border = '2px solid red' left = '15%' bottom = '21%' position={'absolute'} >

                                  <Link zIndex = '2' isExternal href = '#'><Image _hover = {{cursor : 'pointer'}} border = '2px solid' borderRadius={'10px'} bg = 'white' w = '50px' src = 'Netlify logo.png' boxShadow = '2px 2px 10px black'></Image></Link>

                                  <ExternalLinkIcon  />

                                  <Link zIndex = '2' isExternal href = '#' ><Image bg = 'white' _hover = {{cursor : 'pointer'}} borderRadius={'10px'} w = '30px' src = 'github.png' boxShadow = '2px 2px 10px black'></Image></Link>

                                  <ExternalLinkIcon  />


                          </HStack>

                          <Box display = {{base : 'block','848px' : 'none'}} h = '30px' mb = '20px'></Box>


                            <Image zIndex={'2'} w = {{base : '70%','848px' : '20%'}} src = 'Iphone.png'></Image>

                            <Image top={{base : '40%','363px' : '39.6%','468px' : '39%','555px' :'38.5%','631px' : '38%','848px' : '3%'}} left = {{base : '26%','468px' :'27%','848px'  :'64.5%','1349px' : '46.1%'}} w ={{ base : '45%','848px' : '12.8%'}} position={'absolute'} src = 'pharmony+ Mobile.png'></Image>

                    
                    {/* -------------DEscription box---------------------- */}
                    
                    
                            <Box border = '2px solid green' display = {{base : 'none','1349px' : 'block'}}>

                                    <HStack>

                                      <Heading className="project-title" fontSize={'23px'}>Pharmony </Heading>

                                      <Badge  fontSize={10} colorScheme={'green'}>clone : 1mg</Badge>

                                    </HStack>

                                    <Box  bg = 'blue.800' borderRadius={'10px'} mt = '20px' p = '2'>

                                          <Text color = 'aqua' align = 'left'>Description : </Text>

                                          <Text className ="project-description" align = 'left'>It is an Indian healthcare and wellness platform that offers a range of online healthcare services to consumers.</Text>

                                          <Text mt = '5px' color = 'aqua' align = 'left'>Tech Stack : </Text>

                                          <HStack mt = '10px' className="project-tech-stack" mt = '5px'>

                                                  <Image w = '20px' src = 'HTML.png'></Image>

                                                  <Image w = '25px' src = 'CSS.png'></Image>

                                                  <Image borderRadius={'50%'} w = '25px' src = 'Chakra ui.png'></Image>

                                                  <Image borderRadius={'50%'} w = '25px' src = 'React.png'></Image>

                                                  <Image w = '25px' src = 'react-router.png'></Image>

                                          </HStack>

                                          <Text mt = '10px' color = 'aqua' align = 'left'>Deployed Link : </Text>



                                          <HStack mt = '10px'>

                                              <Link isExternal href = '#'><Image _hover = {{cursor : 'pointer'}} border = '2px solid' borderRadius={'10px'} bg = 'white' w = '50px' src = 'Netlify logo.png'></Image></Link>

                                              <ExternalLinkIcon zIndex = '2' />

                                              <Link isExternal href = '#'><Image borderRadius = '40px' bg = 'white' _hover = {{cursor : 'pointer'}} borderRadius={'10px'} w = '30px' src = 'github.png'></Image></Link>

                                              <ExternalLinkIcon zIndex = '2' />


                                            </HStack>




                                    </Box>


                            </Box>


                        </HStack>


                    



                  </GridItem>




            </SimpleGrid>


            </Box>

    </Box>
  )
}

export default Projects